.app {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: #f4f4f4;
}

.app_body {
  display: flex;
  background-color: white;
  height: 95vh;
  width: 95vw;
  border-radius: 0px;
  box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.75);
}

.home-screen {
  display: flex;
  flex-direction: column;
  flex: 0.7;
  justify-content: center;
  align-items: center;
}

.home-screen > p {
  padding: 10px;
  padding-bottom: 15px;
  color: #7e7e7e;
}

.home-screen-about {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 3.5vh;
  padding: 0px;
  color: #7e7e7e;
  align-items: center;
}

.home-screen-about-links:link {
  color: #7e7e7e;
}

.home-screen-about-links:visited {
  color: #7e7e7e;
}

.home-screen-flair-select {
  text-decoration: underline;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 20px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #ddd;
  border-radius: 20px;
  border: 6px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #ccc;
}
